/*Formulario de login*/
.login-form{width: 100%;height: 50px;display: -ms-flexbox;display: flex;-ms-flex-pack: end;justify-content: flex-end;-ms-flex-align: center;align-items: center;-ms-flex-wrap:wrap;flex-wrap:wrap;padding-top:20px;}
.login-form input, .login-form button{height:30px;margin: 0 5px;border-radius: 5px;border: 1px solid white;background-color: #0005;outline: none;}
.login-form input{padding-left: 5px;font-size: 1.3rem;color:white;}
.login-form button{color:white;border-radius: 5px;padding: 5px 30px;cursor:pointer;}
.login-form button:hover{background-color: #fff9;color:rgb(7, 51, 24);}


